import { registerDecorators as _registerDecorators } from "lwc";
import _tmpl from "./app.html";
import { registerComponent as _registerComponent } from "lwc";
import { LightningElement } from 'lwc';

class App extends LightningElement {
  constructor(...args) {
    super(...args);
    this.isLoading = true;
    this.namespaces = [];
    this.excludes = [];
    this.moreThanOneNamespace = true;
    this.selectedNamespace = null;
  }

  async connectedCallback() {
    try {
      const response = await fetch('demo.json');
      var data = await response.json();
      this.excludes = data.excludes;
      this.namespaces = data.namespaces;

      if (data.namespaces.length === 1) {
        this.moreThanOneNamespace = false;
      }

      this.selectedNamespace = data.namespaces[0];
      this.isLoading = false;
    } catch (e) {
      this.isLoading = false;
    }
  }

}

_registerDecorators(App, {
  track: {
    isLoading: 1,
    namespaces: 1,
    excludes: 1,
    moreThanOneNamespace: 1,
    selectedNamespace: 1
  }
})

export default _registerComponent(App, {
  tmpl: _tmpl
});